import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filterDescriptor'
})
export class FilterDescriptorPipe implements PipeTransform {

  transform(items: any[], filterQuery: any): any[] {
    const resultPosts = [];
    if (!filterQuery) return items;
      else{
      for(const post of items){
        if(post.claveSubcompetencia.toLowerCase().indexOf(filterQuery.toLowerCase()) !== -1
        || post.nivel4.toLowerCase().indexOf(filterQuery.toLowerCase()) !== -1
        || post.nivel3.toLowerCase().indexOf(filterQuery.toLowerCase()) !== -1
        || post.nivel2.toLowerCase().indexOf(filterQuery.toLowerCase()) !== -1
        || post.nivel1.toLowerCase().indexOf(filterQuery.toLowerCase()) !== -1
        || post.nivel0.toLowerCase().indexOf(filterQuery.toLowerCase()) !== -1)
        {
          resultPosts.push(post);
        };
      };
      return resultPosts;
    }
  }

}

