import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { PruebaComponent } from './prueba/prueba.component';
import  AteneaService  from './Services/Atenea-service.service';
import { MenuComponent } from './menu/menu.component';
import { DescriptoresComponent } from './descriptores/descriptores.component';
import { EditDescriptorComponent } from './edit-descriptor/edit-descriptor.component';
import { FilterDescriptorPipe } from './filter-descriptor.pipe';
import { FormsModule } from '@angular/forms';

const appRoutes: Routes = [
	{ path: '', redirectTo: '/', pathMatch: 'full' },
	{ path: 'descriptores', component: DescriptoresComponent },
  { path: 'edit-descriptores', component: EditDescriptorComponent }

];

@NgModule({
  declarations: [
    AppComponent,
    PruebaComponent,
    MenuComponent,
    DescriptoresComponent,
    EditDescriptorComponent,
    FilterDescriptorPipe
  ],
  imports: [
    HttpClientModule,
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(
			appRoutes,
			{ enableTracing: true } // <-- debugging purposes only
		)
  ],
  providers: [AteneaService],
  bootstrap: [AppComponent]
  
})

export class AppModule { }
