import { Component, OnInit } from '@angular/core';

import Prueba from '../Models/Prueba';
import AteneaServiceService from '../Services/Atenea-service.service';

@Component({
  selector: 'app-prueba',
  templateUrl: './prueba.component.html',
  styleUrls: ['./prueba.component.css']
})
export class PruebaComponent implements OnInit {

  Prueba: Array<Prueba>;

  constructor(private _AteneaServiceService: AteneaServiceService) { }

  ngOnInit() {
    console.log("llega Angular");
    this._AteneaServiceService.getAssignment().subscribe(data => {
      this.Prueba = data;
    });
  }

}
