import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

import Prueba from '../Models/Prueba';
import Descriptor from '../Models/Descriptor';

@Injectable()

export default class AteneaServiceService {
  //public API = 'https://localhost:44365/';
  API_ENDPOINT = 'https://localhost:44365/';
  //public JOGGING_RECORDS_ENDPOINT = `${this.API_ENDPOINT}Prueba/GetAssignments`;
  progress: number;
  progressObserver: any;
  jsonObject: Object;
  ok: any;
  nook: any;
  constructor(private http: HttpClient) { }

  getAssignment(): Observable<Array<Prueba>> {
    return this.http.get<Array<Prueba>>(this.API_ENDPOINT + "Prueba/GetAssignments");
  }

  getListDescriptores(): Observable<Array<Descriptor>> {
    return this.http.get<Array<Descriptor>>(this.API_ENDPOINT + "Descriptores/GetListDescriptores");
  }

  agregaDescriptor(_descriptor: Descriptor) {
    const headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    return this.http.post<Descriptor>(this.API_ENDPOINT + "Descriptores/AgregaDescriptor", _descriptor, { headers: headers });
  }
}
