import { Component, OnInit } from '@angular/core';
import Descriptor from '../Models/Descriptor';
import AteneaServiceService from '../Services/Atenea-service.service';

@Component({
  selector: 'app-edit-descriptor',
  templateUrl: './edit-descriptor.component.html',
  styleUrls: ['./edit-descriptor.component.css']
})
export class EditDescriptorComponent implements OnInit {
  _Descriptor: Descriptor = new Descriptor();
  respuesta : any;
  constructor(private _AteneaServiceService: AteneaServiceService) { }

  ngOnInit() {

    console.log("Editar");
  }

  agregarDescriptor()
  {

    this._AteneaServiceService.agregaDescriptor(this._Descriptor).subscribe(data => {
      console.log(data);
      this.respuesta = data;
    });

  }

}
